@import "../helpers/FontMaster";
.blog-date {
  font-family: $font-display;
  font-style: italic;
  //   letter-spacing: 0.1em;
  font-size: 0.9em;
  color: $light-gray-text;
}
.blog-title {
  font-family: $font-display;
  // font-size: 1.5em;
  font-weight: 700;
  font-style: normal;
  line-height: 1.5;
  color: black;
}
.blog-content {
  font-family: $font-alata;
  letter-spacing: 0.1em;
  font-size: 0.9em;
  color: $gray-text;
  font-weight: 400;
}
.blog-more {
  font-family: $font-alata;
  //   letter-spacing: 0.1em;
  font-size: 0.9em;
  color: $gray-text;
  font-weight: 400;
}
.side-blog-img {
  height: 100%;
}

// style for single blog post
.post-detail {
  .blog-quote {
    line-height: 2em;
    letter-spacing: 0.1em;
    color: #696969;
    font-style: italic;
    font-family: $font-display;
    border-color: #8eb359 !important;
    border-left: 2px solid;
  }
  .post-category {
    font-family: $font-display;
    font-size: 14px;
    font-style: italic;
    letter-spacing: 0.1em;
    font-weight: 400;
    line-height: 1.8;
    text-decoration: none;
    color: #696969;
  }
  .post-category:hover {
    color: $main-green-text;
  }
  .social-share {
    a {
      color: $gray-text;
    }
    a:hover {
      color: snow;
      background-color: $main-green-text;
    }
    .network-bg {
      width: 40px;
      height: 40px;
    }
  }
}
